import { MenuItem } from '@material-ui/core'
import AppBar from '@material-ui/core/AppBar'
import Avatar from '@material-ui/core/Avatar'
import Button from '@material-ui/core/Button'
import { teal } from '@material-ui/core/colors'
import Menu from '@material-ui/core/Menu'
import { createStyles, withStyles, WithStyles } from '@material-ui/core/styles'
import { Theme } from '@material-ui/core/styles/createMuiTheme'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import History from '@material-ui/icons/History'
import InputIcon from '@material-ui/icons/Input'
import PhoneIcon from '@material-ui/icons/Phone'
import VpnKey from '@material-ui/icons/VpnKey'
import React, { PureComponent } from 'react'
import { Link } from 'react-router-dom'
import { USER_CREDIT_HISTORY } from '../graphql/query/remote'
import filter from '../utils/filter'
import ChangeContact from './Dialogs/ChangeContact'
import ChangePassword from './Dialogs/ChangePassword'
import CreditHistory from './Dialogs/CreditHistory'
import DrawerMenu from './DrawerMenu'
import { CreateMenuItem } from './EnhancedMenu'

const styles = (theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    flex: {
      flex: 1,
    },
    orangeAvatar: {
      margin: 10,
      backgroundColor: '#fff',
    },
    innerAvatar: {
      marginRight: 10,
      backgroundColor: 'black',
    },
    transactionHistory: {
      marginRight: 10,
      // backgroundColor: 'black'
    },
    menuButton: {
      marginLeft: -12,
      marginRight: 20,
    },
    appBar: {
      backgroundColor:
        process.env.NODE_ENV === 'development'
          ? theme.palette.error.dark
          : undefined,
    },
    managerAppBar: {
      backgroundColor:
        process.env.NODE_ENV === 'development'
          ? theme.palette.error.dark
          : teal[300],
    },
  })

const APP_NAME = 'Real Estate'

interface IHeaderProps extends WithStyles<typeof styles> {
  client: any
  user: any
  me?: any
  pageTitle?: string
}
interface IHeaderState {
  anchorEl: null
  dialog: boolean
}
class Header extends PureComponent<IHeaderProps, IHeaderState> {
  state = {
    anchorEl: null,
    dialog: false,
  }

  handleClick = (event: any) => {
    this.setState({ anchorEl: event.currentTarget })
  }

  handleClose = () => {
    this.setState({ anchorEl: null })
  }
  render() {
    const { classes, client, pageTitle = APP_NAME, ...restProps } = this.props
    const { user, me } = this.props
    const { anchorEl } = this.state
    return (
      <header>
        <AppBar
          className={
            (user && user.isAdmin() && classes.appBar) || classes.managerAppBar
          }
        >
          <Toolbar>
            {(() => {
              if (user && user.isAdmin()) {
                return (
                  <DrawerMenu {...restProps} title={pageTitle || APP_NAME} />
                )
              }
              // else if (me && me.stockPermission) {
              //   return (
              //     <DrawerMenu {...this.props} title={pageTitle || APP_NAME} />
              //   );
              // }
              else {
                return null
              }
            })()}
            <Typography
              variant='title'
              color='inherit'
              className={classes.flex}
              component={Link as any}
              {...{ to: '/' }}
            >
              {pageTitle}
            </Typography>
            <Avatar className={classes.orangeAvatar}>
              <Button
                color='primary'
                aria-owns={anchorEl ? 'simple-menu' : undefined}
                aria-haspopup='true'
                onClick={this.handleClick}
              >
                {(me && me.username[0]) || '-'}
              </Button>
            </Avatar>
            <Menu
              id='simple-menu'
              anchorEl={anchorEl}
              open={Boolean(anchorEl)}
              onClose={this.handleClose}
            >
              <MenuItem>
                <Avatar className={classes.innerAvatar}>
                  {me.username[0].toUpperCase()}
                </Avatar>
                {me.username.split(' ')[0]}
              </MenuItem>
              {user.isManager() && (
                <CreditHistory
                  selected={me.id}
                  historyQuery={USER_CREDIT_HISTORY}
                >
                  {(onClick: any) => {
                    return (
                      <MenuItem onClick={onClick} title='Transaction History'>
                        <History className={classes.transactionHistory} />
                        <span>Transaction History</span>
                      </MenuItem>
                    )
                  }}
                </CreditHistory>
              )}
              <ChangeContact
                {...restProps}
                positiveHandler={(fn, args) => {
                  return fn({
                    variables: {
                      id: me.id,
                      ...args,
                    },
                  })
                }}
              >
                <CreateMenuItem icon={<PhoneIcon />} content='Change Contact' />
              </ChangeContact>
              <ChangePassword
                {...restProps}
                positiveHandler={(fn, args) => {
                  return fn({
                    variables: {
                      id: user.id,
                      data: filter(args, ['confirmNewPassword']),
                    },
                  })
                }}
              >
                <CreateMenuItem icon={<VpnKey />} content='Change password' />
              </ChangePassword>
              <CreateMenuItem
                {...{
                  onClick: async () => {
                    this.handleClose()
                    await window.localStorage.clear()
                    if ('serviceWorker' in navigator) {
                      const keys = await caches.keys()
                      keys.forEach(cacheName => {
                        caches.delete(cacheName)
                      })
                    }
                    client.resetStore()
                    window.location.href = '/'
                  },
                }}
                icon={<InputIcon />}
                content='Logout'
              />
            </Menu>
          </Toolbar>
        </AppBar>
      </header>
    )
  }
}

export default withStyles(styles)(Header)
