import {
  Checkbox,
  TableCell,
  TableHead,
  TableRow,
  TableSortLabel,
  Tooltip,
} from '@material-ui/core'
import React from 'react'
import getColor from '../../../utils/getColor'

function getOrder(order: string | undefined) {
  return order === 'asc' ? 'asc' : 'desc'
}

interface IEnhancedTableHeadProps {
  numSelected: number
  onRequestSort: Function
  onSelectAllClick: (e: any, checked: boolean) => void
  order: string
  orderBy: string
  rowCount: number
  selectable: boolean
  columnData: any[]
}

export default class EnhancedTableHead extends React.PureComponent<
  IEnhancedTableHeadProps
> {
  createSortHandler = (property: any) => () => {
    this.props.onRequestSort(event, property)
  }

  render() {
    const {
      selectable,
      numSelected,
      onSelectAllClick,
      order,
      orderBy,
      rowCount,
      columnData,
    } = this.props
    const color = getColor()
    let first = true
    return (
      <TableHead>
        <TableRow>
          {selectable ? (
            <TableCell padding='checkbox'>
              <Checkbox
                indeterminate={numSelected > 0 && numSelected < rowCount}
                checked={numSelected === rowCount}
                onChange={onSelectAllClick}
              />
            </TableCell>
          ) : null}
          {columnData.map((column, index) => {
            let colorValue = null
            if (first && column.id === 'createdAt') {
              colorValue = null
              first = false
            } else {
              colorValue = color.next().value
            }
            return (
              <TableCell
                key={column.id}
                // numeric={column.numeric}
                padding={
                  index !== 0 && column.disablePadding ? 'none' : 'default'
                }
                sortDirection={orderBy === column.id ? getOrder(order) : false}
                colSpan={column.colSpan}
              >
                <Tooltip
                  title='Sort'
                  placement={column.numeric ? 'bottom-end' : 'bottom-start'}
                  enterDelay={300}
                >
                  <TableSortLabel
                    active={orderBy === column.id}
                    direction={getOrder(order)}
                    onClick={this.createSortHandler(column.id)}
                    style={{ color: colorValue as string }}
                  >
                    {column.label}
                  </TableSortLabel>
                </Tooltip>
              </TableCell>
            )
          }, this)}
        </TableRow>
      </TableHead>
    )
  }
}
