import TableCell from '@material-ui/core/TableCell'
import React from 'react'
import { ShowHide } from '../components/ShowHide'
import toCurrency from './toCurrency'

export const POSITIVE_COLOR = 'rgba(51, 153, 51, 1)'
export const NEGATIVE_COLOR = 'rgba(255, 0, 0, 0.4)'
export const BACKGROUND_COLOR = 'rgba(0, 255, 0, 0.4)'
export const NEGATIVE_TEXT_COLOR = 'rgba(255, 0, 0, 1)'

export const RENDER_VALUE = ({
  key = '',
  value,
  skip = [],
  balance = true,
}: {
  key?: string
  value: any
  skip?: string[]
  balance?: boolean
}) => {
  let overrideColor = null
  if (key.includes('available')) {
    overrideColor = value < 0 ? NEGATIVE_TEXT_COLOR : POSITIVE_COLOR
  } else if (balance && key.includes('balance')) {
    overrideColor = value < 0 ? NEGATIVE_COLOR : POSITIVE_COLOR
  } else if (!balance) {
    overrideColor = value < 0 ? NEGATIVE_COLOR : POSITIVE_COLOR
  }
  if (typeof value === 'number' && !skip.includes(key)) {
    value = toCurrency(value)
  } else if (typeof value === 'boolean') {
    if (value) {
      value = <span style={{ color: 'green', fontSize: '1.5rem' }}></span>
    } else {
      value = ''
    }
  }
  return { value, color: overrideColor }
}

export default ({
  match = '',
  bgcolor = BACKGROUND_COLOR,
  alt = false,
  altColor = NEGATIVE_COLOR,
  altValue = null,
  renderValue = RENDER_VALUE,
  classes = () => '',
}: {
  match?: string
  bgcolor?: string
  alt?: boolean
  altColor?: string
  altValue?: any
  renderValue?: Function
  classes?: Function
} = {}) => (key: string, items: any, colorValue?: string) => {
  if (!(items instanceof Object)) {
    const val = renderValue({ value: items, key })
    return (
      <TableCell
        className={classes(key)}
        style={{
          color: val.color || colorValue,
          backgroundColor:
            key === match
              ? alt
                ? val.value === altValue
                  ? altColor
                  : bgcolor
                : bgcolor
              : undefined,
        }}
        key={key || Math.random()}
      >
        {key === 'note' ? <ShowHide content={val.value} /> : val.value}
      </TableCell>
    )
  }
  const { id, __typename, paid, ...rest } = items
  return Object.keys(rest).map(e => {
    const val = renderValue({ value: rest[e], key: e })
    return (
      <TableCell
        style={{
          color: val.color || colorValue,
          backgroundColor: paid ? bgcolor : undefined,
        }}
        key={key ? key + id + e : id + e}
      >
        {val.value}
      </TableCell>
    )
  })
}
