import { FormHelperText } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import CircularProgress from '@material-ui/core/CircularProgress'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import { withStyles } from '@material-ui/core/styles'
import { Form } from 'informed'
import React from 'react'
import { UPDATE_USER_PASSWORD } from '../../graphql/mutation/remote'
import filter from '../../utils/filter'
import { getJSX } from '../forms/FormGenerator'
import Mutation from '../Mutation'
import CommanDialog, { IProps, styles } from './CommanDialog'

const defaults = {
  currentPassword: {
    type: 'password',
    default: '',
    label: 'Current password',
  },
  newPassword: {
    type: 'password',
    default: '',
    label: 'New password',
  },
  confirmNewPassword: {
    type: 'password',
    default: '',
    label: 'Confirm new password',
    validate: (val: string, vals: any) => {
      return val !== vals.newPassword ? 'Password does not match' : null
    },
  },
}

interface IChangePassword extends IProps {
  reset?: boolean
}

export default withStyles(styles)(
  class ChangePassword extends CommanDialog<IChangePassword> {
    render() {
      const { open } = this.state
      const {
        classes,
        history,
        negativeHandler = () => this.handleClose(),
        positiveHandler = () => null,
        postMutation = () => null,
        reset = false,
        children,
      } = this.props
      const title = 'Change password'
      const content = 'Change password amount to this user.'
      return (
        <Mutation mutation={UPDATE_USER_PASSWORD}>
          {(mutate: Function, { loading, error, data }: IGraphqlData) => {
            return (
              <React.Fragment>
                {React.cloneElement(children, { onClick: this.handleOpen })}
                {open ? (
                  <Dialog
                    fullScreen={false}
                    open={open}
                    onClose={negativeHandler}
                    aria-labelledby={title}
                    title={title}
                  >
                    <Form
                      onSubmit={(e: any) => {
                        positiveHandler(mutate, e).then((data: any) => {
                          if (postMutation) {
                            postMutation(data)
                            this.handleClose()
                          } else {
                            history.goBack()
                          }
                        })
                      }}
                    >
                      <DialogTitle id={title}>{title}</DialogTitle>
                      <DialogContent>
                        <DialogContentText>{content}</DialogContentText>
                        {getJSX(
                          reset
                            ? filter(defaults, ['currentPassword'])
                            : defaults,
                        )}
                      </DialogContent>
                      <DialogActions>
                        {loading ? (
                          <CircularProgress className={classes.progress} />
                        ) : null}
                        {error ? (
                          <FormHelperText error={true}>
                            {error.message}
                          </FormHelperText>
                        ) : null}
                        <Button
                          size='large'
                          color='secondary'
                          onClick={negativeHandler}
                        >
                          Cancel
                        </Button>
                        <Button
                          size='large'
                          color='primary'
                          type='submit'
                          disabled={loading}
                        >
                          Confirm
                        </Button>
                      </DialogActions>
                    </Form>
                  </Dialog>
                ) : null}
              </React.Fragment>
            )
          }}
        </Mutation>
      )
    }
  },
)

