import React, { Component } from 'react'
import { Route, Switch } from 'react-router-dom'
import asyncImport from './components/AsyncImport'
import Login from './components/forms/Login'
import { _404 as _404Page } from './pages/404'

const _404 = asyncImport(() => import('./components/404'))
const Index = asyncImport(() => import('./pages/Index'))
// const Login = asyncImport(() => import( './pages/Login'));

const User = asyncImport(() => import('./pages/User'))
const CreateUserForm = asyncImport(() =>
  import('./components/forms/CreateUser'),
)

const Site = asyncImport(() => import('./pages/Site'))
const SiteForm = asyncImport(() => import('./components/forms/Site'))
const SiteDetail = asyncImport(() => import('./pages/SiteDetail'))

const SiteDetailForm = asyncImport(() => import('./components/forms/SiteEntry'))

const Property = asyncImport(() => import('./pages/Property'))
const CreatePropertyForm = asyncImport(() =>
  import('./components/forms/CreateProperty'),
)

const Activity = asyncImport(() => import('./pages/Activity'))

interface IRoute {
  index: Function
  create: Function
  update: Function
  detail: Function
}

function routeCreator(name: string): IRoute {
  return {
    index: () => `/${name}`,
    create: () => `/${name}/create`,
    update: ({ id, edit }: { id: string; edit?: boolean } = { id: ':id?' }) =>
      `/${name}${edit ? '/edit' : edit === undefined ? '/:edit' : ''}/${id}`,
    detail: ({ id }: { id: string } = { id: ':id' }) => `/${name}/detail/${id}`,
  }
}

export const Routes = {
  root: () => `/`,
  index: () => `/`,
  home: () => `/`,
  login: () => `/login`,
  Site: {
    ...routeCreator('site'),
  },
  SiteEntry: {
    create: (
      { siteId, edit, id }: { siteId: string; edit?: boolean; id?: string } = {
        siteId: ':siteId',
        id: ':id?',
      },
    ) =>
      `/siteEntry/create/${siteId}${
        edit ? '/edit' : edit === undefined ? '/:edit?' : ''
      }${id ? '/' + id : id === undefined ? '/:id?' : ''}`,
  },
  User: {
    ...routeCreator('user'),
    // create: () => `/user/create`,
  },
  Property: {
    ...routeCreator('property'),
    // create: () => `/property/create`,
    // update: ({ id = ':id?', edit } = {}) => `/property${edit ? '/edit' : (edit === undefined ? '/:edit?' : '')}/${id ? id : (id === undefined ? '/:id?' : '')}`
  },
  Activity: {
    ...routeCreator('activity'),
  },
  // Stock: {
  //   ...routeCreator('stock'),
  // },
  StockSupplier: {
    ...routeCreator('stockSupplier'),
  },
  StockTransaction: {
    ...routeCreator('stockTransaction'),
  },
}
class Router extends Component {
  render() {
    return (
      <Switch {...this.props}>
        <Route
          exact={true}
          path={Routes.root()}
          component={Index}
          {...this.props}
        />
        <Route
          exact={true}
          path={Routes.login()}
          component={Login}
          {...this.props}
        />

        {/* the question mark (?) at the end is for optional arguments */}
        <Route path={Routes.Site.index()}>
          <Switch>
            <Route
              exact={true}
              path={Routes.Site.detail()}
              component={SiteDetail}
              {...this.props}
            />
            <Route
              exact={true}
              path={Routes.Site.create()}
              component={SiteForm}
              {...this.props}
            />
            <Route
              exact={true}
              path={Routes.Site.update()}
              component={SiteForm}
              {...this.props}
            />
            <Route component={Site} {...this.props} />
          </Switch>
        </Route>

        <Route
          exact={true}
          path={Routes.SiteEntry.create()}
          component={SiteDetailForm}
          {...this.props}
        />
        <Route path={Routes.User.index()}>
          <Switch>
            <Route
              exact={true}
              path={Routes.User.create()}
              component={CreateUserForm}
              {...this.props}
            />
            <Route
              exact={true}
              path={Routes.User.update()}
              component={CreateUserForm}
              {...this.props}
            />
            <Route component={User} {...this.props} />
          </Switch>
        </Route>
        <Route path={Routes.Property.index()}>
          <Switch>
            <Route
              exact={true}
              path={Routes.Property.update()}
              component={CreatePropertyForm}
              {...this.props}
            />
            <Route
              exact={true}
              path={Routes.Property.create()}
              component={CreatePropertyForm}
              {...this.props}
            />
            <Route component={Property} {...this.props} />
          </Switch>
        </Route>
        <Route path={Routes.Activity.index()}>
          <Switch>
            <Route component={Activity} {...this.props} />
          </Switch>
        </Route>
        {/* <Route path={Routes.Stock.index()}>
          <Switch>
            <Route
              exact={true}
              path={Routes.Stock.detail()}
              component={StockItemTransaction}
              {...this.props}
            />
            <Route component={StockDashboard} {...this.props} />
          </Switch>
        </Route> */}
        {/* <Route path={Routes.StockSupplier.index()}>
          <Switch>
            <Route exact={true} path={Routes.StockSupplier.detail()} component={SupplierTransaction} {...this.props} />
            <Route exact={true} path={Routes.StockSupplier.update()} component={CreateStockSupplierForm} {...this.props} />
            <Route exact={true} path={Routes.StockSupplier.create()} component={CreateStockSupplierForm} {...this.props} />
          </Switch>
        </Route>
        <Route exact={true} path={Routes.StockTransaction.create()} component={CreateStockTransactionForm} {...this.props} /> */}

        <Route path={'/404'} component={() => <_404Page />} {...this.props} />
        <Route component={_404} {...this.props} />
      </Switch>
    )
  }
}

export default Router
