import { Typography } from '@material-ui/core'
import React, { useState } from 'react'
interface IShowHideProps {
  content: string
}
export const ShowHide = (props: IShowHideProps) => {
  const [show, setShow] = useState(false)
  const { content = '' } = props
  console.log(content)
  if (content && content !== '-') {
    return (
      <>
        {show ? content : content.substr(0, 10)}
        <Typography
          color='primary'
          style={{ cursor: 'pointer' }}
          onClick={() => setShow(!show)}
        >
          {!show ? '...show' : 'hide'}
        </Typography>
      </>
    )
  } else if (content === '-') {
    return <>{content}</>
  } else {
    return null
  }
}
