import { gql } from 'apollo-boost'

export const LOCAL_LOGIN = gql`
  query localLogin {
    localLogin @client {
      token
    }
  }
`

function tableQueryGenerator(name: string): string {
  return gql`
    query ${name} {
        ${name} @client{
            order,
            orderBy,
            selected,
            page,
            rowsPerPage
        }
    }
`
}

export const ENTRY_TABLE = tableQueryGenerator('entryTable')

export const SITE_TABLE = tableQueryGenerator('siteTable')

export const USER_ENTRY_TABLE = tableQueryGenerator('userEntryTable')

export const CREDIT_ENTRY_TABLE = tableQueryGenerator('creditEntryTable')

export const PROPERTY_TABLE = tableQueryGenerator('propertyTable')

export const DUE_PROPERTY_TABLE = tableQueryGenerator('duePropertyTable')

export const STOCK_ITEM_TABLE = tableQueryGenerator('stockItemTable')

export const STOCK_SUPPLIER_TABLE = tableQueryGenerator('stockSupplierTable')

export const STOCK_ITEM_TRANSACTION_TABLE = tableQueryGenerator(
  'stockItemTransactionTable',
)

export const SUPPLIER_TRANSACTION_TABLE = tableQueryGenerator(
  'supplierTransactionTable',
)

export const STOCK_TRANSACTION_TABLE = tableQueryGenerator(
  'stockTransactionTable',
)

export const ENHANCED_TABS = gql`
  query enhancedTabs {
    enhancedTabs @client {
      value
    }
  }
`

export const STOCK_ENHANCED_TABS = gql`
  query stockEnhancedTabs {
    stockEnhancedTabs @client {
      value
    }
  }
`
