import IconButton from '@material-ui/core/IconButton'
import History from '@material-ui/icons/History'
import React from 'react'
import { CREDIT_ENTRY_TABLE } from '../../graphql/mutation/local'
import { CREDIT_ENTRY_TABLE as CREDIT_ENTRY_TABLE_QUERY } from '../../graphql/query/local'
import { USER_CREDIT_HISTORY } from '../../graphql/query/remote'
import { defaultState } from '../../graphql/resolvers'
import commanRender, { RENDER_VALUE } from '../../utils/commanRender'
import getContactJSX from '../../utils/getContactJSX'
import EnhancedTable from '../Enhanced/Table/Table'
import TableWrapper from '../TableWrapper'
import CommanDialog, { ICommanDialogProps } from './CommanDialog'
import Dialog from './Dialog'

const creditColumnData = [
  {
    id: 'createdAt',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Created On',
  },
  {
    id: 'amount',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Amount ()',
  },
  {
    id: 'type',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Transaction Type',
  },
  {
    id: 'balance',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Balance ()',
  },
  {
    id: 'note',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Note',
  },
]
const creditExclude = ['__typename', 'count']

interface ICreditHistory extends ICommanDialogProps {
  skipColumns?: string[]
  positiveText?: string
  historyQuery: string
  selected: any
}
class CreditHistory extends CommanDialog<ICreditHistory> {
  render() {
    const { open } = this.state
    const {
      skipColumns = [],
      selected,
      positiveText = 'Ok',
      positiveHandler,
      historyQuery = USER_CREDIT_HISTORY,
      children = (onClick: any) => (
        <IconButton
          aria-label='Transaction History'
          title='Transaction History'
          onClick={onClick}
        >
          <History />
        </IconButton>
      ),
    } = this.props
    const { creditEntryTable } = defaultState
    return (
      <div>
        {children(this.handleOpen)}
        {open ? (
          <Dialog
            fullScreen={false}
            open={open}
            title={'Transaction History'}
            positiveText={positiveText}
            negativeHandler={e => this.handleClose()}
            positiveHandler={e => {
              this.handleClose()
              positiveHandler()
            }}
            content={
              <TableWrapper
                tableMutation={CREDIT_ENTRY_TABLE}
                tableQuery={CREDIT_ENTRY_TABLE_QUERY}
                dataQuery={historyQuery}
                defaultState={creditEntryTable}
                getQueryVariables={() => ({ id: selected })}
                resetOnMount={true}
              >
                {(
                  tableSettings: typeof creditEntryTable,
                  lmutate: Function,
                  { loading, data, refetch }: IGraphqlData,
                ) => {
                  const creditHistory = data[Object.keys(data)[0]]
                  return (
                    <EnhancedTable
                      {...this.props}
                      loading={loading}
                      data={creditHistory}
                      title='Users Credit History'
                      settings={tableSettings}
                      skipToolbar={true}
                      count={
                        (creditHistory.length && creditHistory[0].count) || 0
                      }
                      columnData={creditColumnData.filter(
                        col => !skipColumns.includes(col.id),
                      )}
                      exclude={creditExclude}
                      render={commanRender({
                        match: 'type',
                        alt: true,
                        altValue: 'DEBIT',
                        altColor: '#eee',
                        renderValue: ({ key, value }: any) => {
                          if (key === 'contact') {
                            return { value: getContactJSX(value) }
                          }
                          const result = RENDER_VALUE({
                            key,
                            value,
                            skip: ['contact'],
                          })
                          return {
                            ...result,
                            value: String(value).length ? result.value : '-',
                          }
                        },
                      })}
                      mutate={async (args: any) => {
                        await lmutate({ variables: args })
                        // refetch();
                      }}
                    />
                  )
                }}
              </TableWrapper>
            }
          />
        ) : null}
      </div>
    )
  }
}

export default CreditHistory
