import {
  Grid,
  IconButton,
  Toolbar,
  Tooltip,
  withStyles,
  WithStyles,
} from '@material-ui/core'
import { lighten } from '@material-ui/core/styles/colorManipulator'
import { Theme } from '@material-ui/core/styles/createMuiTheme'
import Typography from '@material-ui/core/Typography'
import AddIcon from '@material-ui/icons/Add'
import EditIcon from '@material-ui/icons/Edit'
import Visibility from '@material-ui/icons/Visibility'
import classNames from 'classnames'
import React from 'react'
import CreditHistory from '../../Dialogs/CreditHistory'
import DeleteDialog from '../../Dialogs/DeleteDialog'
import FormDialog from '../../Dialogs/FormDialog'

const toolbarStyles = (theme: Theme) => ({
  root: {
    paddingRight: theme.spacing.unit,
  },
  highlight:
    theme.palette.type === 'light'
      ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85),
        }
      : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark,
        },
  spacer: {
    flex: '1 1',
  },
  actions: {
    color: theme.palette.text.secondary,
  },
  title: {
    flex: '0 0 auto',
  },
})

interface IToolbarProps extends WithStyles<typeof toolbarStyles> {
  skipHistoryColumns?: string[]
  numSelected: number
  selected: any[]
  title: string
  menu: any
  historyable?: string
  options: {
    onView?: Function
    onEdit?: Function
    onDelete?: Function
    onCredit?: Function
    onAdd?: (e?: any) => void

    viewable?: boolean
    editable?: boolean
    deletable?: boolean
    creditable?: boolean
    addable?: boolean
  }
  primaryButtonIcon?: any
  credit?: Function
}
const TableToolbar = (props: IToolbarProps) => {
  const {
    skipHistoryColumns,
    numSelected,
    selected,
    classes,
    title,
    menu,
    historyable,
    options: { onView, onEdit, onDelete, onCredit, onAdd },
    // (lselected: string, lonCredit: any) =>
    credit = (
      <FormDialog
        positiveHandler={(m: any, e: any) =>
          onCredit && onCredit(m, { ...e, id: selected[0] })
        }
      />
    ),
    primaryButtonIcon = <AddIcon />,
  } = props
  const {
    viewable = onView !== onEdit,
    editable = onEdit ? true : false,
    deletable = onDelete ? true : false,
    creditable = onCredit ? true : false,
    addable = onAdd ? true : false,
  } = props.options

  const deleteJSX = (
    <Grid item={true} xs={true}>
      <Tooltip title='Delete'>
        <DeleteDialog
          positiveHandler={() => onDelete && onDelete({ ids: selected })}
        />
      </Tooltip>
    </Grid>
  )
  return (
    <Toolbar
      className={classNames(classes.root, {
        [classes.highlight]: numSelected > 0,
      })}
    >
      <div className={classes.title}>
        {numSelected > 0 ? (
          <Typography color='inherit' variant='subheading'>
            {numSelected} selected
          </Typography>
        ) : (
          <Typography variant='title' id='tableTitle'>
            {title}
          </Typography>
        )}
      </div>
      <div className={classes.spacer} />
      <div className={classes.actions}>
        <Grid container={true} direction='row' justify='space-between'>
          {numSelected > 0 ? (
            <React.Fragment>
              {numSelected === 1 ? (
                <React.Fragment>
                  {historyable ? (
                    <Grid item={true} xs={true}>
                      <CreditHistory
                        skipColumns={skipHistoryColumns}
                        selected={selected[0]}
                        historyQuery={historyable}
                      />
                    </Grid>
                  ) : null}
                  {creditable ? (
                    <Grid item={true} xs={true}>
                      <Tooltip title='Credit'>
                        {React.cloneElement(
                          credit instanceof Function
                            ? credit({ selected: selected[0], onCredit })
                            : credit,
                          { selected: selected[0] },
                        )}
                      </Tooltip>
                    </Grid>
                  ) : null}
                  {viewable ? (
                    <Grid item={true} xs={true}>
                      <IconButton
                        aria-label='View'
                        title='View'
                        onClick={e => onView && onView(selected[0])}
                      >
                        <Visibility />
                      </IconButton>
                    </Grid>
                  ) : null}
                  {editable ? (
                    <Grid item={true} xs={true}>
                      <IconButton
                        aria-label='Edit'
                        title='Edit'
                        onClick={e => onEdit && onEdit(selected[0])}
                      >
                        <EditIcon />
                      </IconButton>
                    </Grid>
                  ) : null}
                  {deletable ? deleteJSX : null}
                </React.Fragment>
              ) : (
                <React.Fragment>{deletable ? deleteJSX : null}</React.Fragment>
              )}
            </React.Fragment>
          ) : addable ? (
            <IconButton
              color='primary'
              style={{ backgroundColor: '#3f51b5', color: 'white' } as any}
              aria-label='Add'
              title='Add'
              onClick={onAdd}
            >
              {primaryButtonIcon}
            </IconButton>
          ) : // <Button variant="fab" mini color="secondary" aria-label="Add" raised={false} onClick={onAdd}><AddIcon /></Button>
          null}
          <Grid item={true} xs={true}>
            {menu ? menu(selected) : null}
          </Grid>
        </Grid>
      </div>
    </Toolbar>
  )
}

const EnhancedTableToolbar = withStyles(toolbarStyles)(TableToolbar)
export default EnhancedTableToolbar
