import { gql } from 'apollo-boost'

export const typeDefs = gql`
  type SiteEntryOutput {
    quantity: Int!
    cost: Int!
  }
  input SiteEntryInput {
    quantity: Int!
    cost: Int!
  }
`

const resolveRepeater = (list: string[]) => {
  function jsUcfirst(str: string) {
    return str.charAt(0).toUpperCase() + str.slice(1)
  }
  const result: { defaults: any; resolvers: any } = {
    defaults: {},
    resolvers: {},
  }
  list.forEach(e => {
    const type = jsUcfirst(e)
    result.defaults[e] = {
      __typename: type,
      order: 'desc',
      orderBy: 'id',
      selected: [],
      page: 0,
      rowsPerPage: 10,
    }
    result.resolvers[e] = async (_: any, args: any, { cache }: any) => {
      const data = {
        [e]: {
          __typename: type,
          ...args,
        },
      }
      await cache.writeData({ data })
      return null
    }
  })
  return result
}

const R = resolveRepeater([
  'entryTable',
  'entryTotalTable',
  'siteTable',
  'creditEntryTable',
  'userEntryTable',
  'propertyTable',
  'duePropertyTable',
  'propertyTableTotal',
  'stockItemTable',
  'stockSupplierTable',
  'stockItemTransactionTable',
  'stockTransactionTable',
  'supplierTransactionTable',
])

export const defaultState = {
  networkStatus: {
    __typename: 'NetworkStatus',
    isConnected: true,
  },
  localLogin: {
    __typename: 'LocalLogin',
    token: window.localStorage.getItem('userToken'),
  },
  enhancedTabs: {
    __typename: 'EnhancedTabs',
    value: 0,
  },
  stockEnhancedTabs: {
    __typename: 'StockEnhancedTabs',
    value: 0,
  },
  ...R.defaults,
}

export const resolvers = {
  Mutation: {
    updateNetworkStatus: (_: any, { isConnected }: any, { cache }: any) => {
      const data = {
        networkStatus: {
          __typename: 'NetworkStatus',
          isConnected,
        },
      }
      cache.writeData({ data })
      return null
    },
    localLogin: async (_: any, { token }: any, { cache }: any) => {
      const data = {
        localLogin: {
          __typename: 'LocalLogin',
          token,
        },
      }
      await cache.writeData({ data })
      return null
    },
    enhancedTabs: async (_: any, args: any, { cache }: any) => {
      const data = {
        enhancedTabs: {
          __typename: 'EnhancedTabs',
          ...args,
        },
      }
      await cache.writeData({ data })
      return null
    },
    stockEnhancedTabs: async (_: any, args: any, { cache }: any) => {
      const data = {
        stockEnhancedTabs: {
          __typename: 'StockEnhancedTabs',
          ...args,
        },
      }
      await cache.writeData({ data })
      return null
    },
    ...R.resolvers,
  },
}
