import { FormHelperText, WithStyles } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import CardContent from '@material-ui/core/CardContent'
import Grid from '@material-ui/core/Grid'
import InputLabel from '@material-ui/core/InputLabel'
import { createStyles, Theme, withStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'
import { Form, Scope } from 'informed'
import React, { PureComponent } from 'react'
import Checkbox from '../formComponents/Checkbox'
import Select from '../formComponents/Select'
import Text from '../formComponents/Text'
import Loader from '../Loader'
import Mutation from '../Mutation'

const styles = (theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
      marginTop: '2rem',
    },
    card: {
      minWidth: 275,
      margin: '0 2%',
    },
    header: {
      marginBottom: '1rem',
      fontSize: '2rem',
    },
    progress: {
      margin: '1rem',
    },
    button: {
      margin: theme.spacing.unit,
    },
  })

export function getJSX(obj: any, index = 1, autoFocusEnabled = false) {
  return Object.keys(obj).map(e => {
    let jsx = null
    if (!obj[e]) {
    } else if (obj[e].type === 'custom') {
      jsx = <>{obj[e].default}</>
    } else if (typeof obj[e].default === 'string') {
      jsx = (
        <Text
          field={e}
          label={obj[e].label || e}
          type={obj[e].type || 'text'}
          validate={obj[e].validate}
          filter={obj[e].filter}
          initialValue={obj[e].default}
          inputProps={{
            step: '0.01',
            min: obj[e].min,
            required: obj[e].required,
          }}
          startAdornment={obj[e].startAdornment || null}
          autoFocus={!autoFocusEnabled}
          validateOnChange={true}
          validateOnBlur={true}
        />
      )
    } else if (typeof obj[e].default === 'number') {
      jsx = (
        <Text
          {...obj[e]}
          field={e}
          label={obj[e].label || e}
          type={'number'}
          validate={obj[e].validate}
          filter={
            obj[e].filter ||
            ((el: any) => {
              if (el === '') {
                return el
              }
              return Number(el)
            })
          }
          initialValue={obj[e].default}
          inputProps={{
            step: '0.01',
            min: obj[e].min,
            required: obj[e].required,
          }}
          startAdornment={obj[e].startAdornment || null}
          endAdornment={obj[e].endAdornment || null}
          autoFocus={!autoFocusEnabled}
          validateOnChange={true}
          validateOnBlur={true}
        />
      )
    } else if (obj[e].default instanceof Array) {
      jsx = (
        <Select
          field={e}
          label={obj[e].label || e}
          options={obj[e].default}
          initialValue={obj[e].current}
          mapping={obj[e].mapping}
          filter={obj[e].filter}
          selected={obj[e].current}
          validate={obj[e].validate}
          startAdornment={obj[e].startAdornment || null}
          validateOnChange={true}
          validateOnBlur={true}
        />
      )
    } else if (obj[e].default instanceof Date) {
      jsx = (
        <Text
          field={e}
          label={obj[e].label || e}
          type={obj[e].type || 'text'}
          validate={obj[e].validate}
          filter={obj[e].filter}
          initialValue={obj[e].default}
          startAdornment={obj[e].startAdornment || null}
          validateOnChange={true}
          validateOnBlur={true}
        />
      )
    } else if (obj[e].type === Boolean) {
      jsx = (
        <Checkbox
          field={e}
          label={obj[e].label || e}
          initialValue={obj[e].default}
          validate={obj[e].validate}
          filter={obj[e].filter}
          validateOnChange={true}
          validateOnBlur={true}
        />
      )
    } else if (obj[e].default instanceof Object) {
      jsx = (
        <Scope scope={e}>
          <Grid
            container={true}
            justify='space-between'
            alignItems='center'
            className='mt4'
          >
            <Grid item={true} xs={3}>
              <InputLabel htmlFor={e} className='ttc mt5'>
                {index}. {obj[e].label || e}
              </InputLabel>
            </Grid>
            <Grid item={true} xs={9}>
              {getJSX(obj[e].default, index + 1, autoFocusEnabled)}
            </Grid>
          </Grid>
          <hr />
        </Scope>
      )
    } else {
      throw new Error('undefined object found in form generator')
    }
    index++
    autoFocusEnabled = true
    return <React.Fragment key={e || Math.random()}>{jsx}</React.Fragment>
  })
}

interface IFormGenerator extends WithStyles<typeof styles> {
  history?: any
  title: string
  submitText?: string
  defaults: any
  mutation: string
  mutateFunc?: (fn: Function, data: any) => any
  postMutation?: Function
  raw?: boolean
}
class FormGenerator extends PureComponent<IFormGenerator> {
  render() {
    const {
      classes,
      history,
      title,
      submitText = 'Submit',
      defaults,
      mutation,
      mutateFunc = (fn: Function, data: any) => {
        return fn({ variables: { data } })
      },
      postMutation,
      raw = false,
    } = this.props
    return (
      <Mutation mutation={mutation}>
        {(mutate: Function, { loading, error, data }: IGraphqlData) => {
          return (
            <Form
              onSubmit={(e: any) => {
                mutateFunc(mutate, e).then((data: any) => {
                  postMutation ? postMutation(data) : history.goBack()
                })
              }}
            >
              <Grid
                container={true}
                className={classes.root}
                justify='center'
                alignItems='center'
              >
                <Grid item={true} xs={12} sm={6} md={5}>
                  <Card className={classes.card}>
                    {loading ? <Loader /> : null}
                    <CardContent className='ph4'>
                      {!raw ? (
                        <Typography
                          variant='display1'
                          className='mb2'
                          align='center'
                        >
                          {title}
                        </Typography>
                      ) : null}
                      {getJSX(defaults)}
                    </CardContent>
                    {!raw ? (
                      <CardActions>
                        <Grid container={true} justify='flex-end'>
                          {error ? (
                            <FormHelperText error={true}>
                              {error.message}
                            </FormHelperText>
                          ) : null}
                          <Button
                            size='large'
                            variant='contained'
                            color='secondary'
                            className={classes.button}
                            onClick={e => history.goBack()}
                            disabled={loading}
                          >
                            Cancel
                          </Button>
                          <Button
                            size='large'
                            variant='contained'
                            color='primary'
                            className={classes.button}
                            type='submit'
                            disabled={loading}
                          >
                            {submitText}
                          </Button>
                        </Grid>
                      </CardActions>
                    ) : null}
                  </Card>
                </Grid>
              </Grid>
            </Form>
          )
        }}
      </Mutation>
    )
  }
}

export default withStyles(styles)(FormGenerator)
