import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import React from 'react'

interface IResponsiveDialog {
  fullScreen: boolean
  open: boolean
  title: string
  content: any
  positiveText?: string
  negativeText?: string
  negativeHandler: (e: any) => void
  positiveHandler: (e: any) => void
}

class ResponsiveDialog extends React.PureComponent<IResponsiveDialog, any> {
  render() {
    const {
      fullScreen,
      open = false,
      title,
      content,
      positiveText = 'Yes',
      negativeText,
      negativeHandler = () => null,
      positiveHandler,
    } = this.props

    return (
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={negativeHandler}
        aria-labelledby={title}
        title={title}
      >
        <DialogTitle id={title}>{title}</DialogTitle>
        <DialogContent>
          {typeof content === 'string' ? (
            <DialogContentText>{content}</DialogContentText>
          ) : (
            content
          )}
        </DialogContent>
        <DialogActions>
          {negativeText ? (
            <Button
              onClick={negativeHandler}
              color='secondary'
              autoFocus={true}
            >
              {negativeText}
            </Button>
          ) : null}
          {positiveHandler ? (
            <Button onClick={positiveHandler} color='primary'>
              {positiveText}
            </Button>
          ) : null}
        </DialogActions>
      </Dialog>
    )
  }
}

export default ResponsiveDialog
