import IconButton from '@material-ui/core/IconButton'
import DeleteIcon from '@material-ui/icons/Delete'
import React from 'react'
import CommanDialog, { ICommanDialogProps } from './CommanDialog'
import Dialog from './Dialog'

class DeleteDialog extends CommanDialog<ICommanDialogProps> {
  render() {
    const { open } = this.state
    const { positiveHandler, negativeHandler = this.handleClose } = this.props
    return (
      <div>
        <IconButton
          aria-label='Delete'
          title='Delete'
          onClick={this.handleOpen}
        >
          <DeleteIcon />
        </IconButton>
        {open ? (
          <Dialog
            fullScreen={false}
            open={open}
            title={'Confirm Delete'}
            content={'Are You sure?'}
            negativeText={'No'}
            negativeHandler={negativeHandler}
            positiveHandler={() => {
              this.handleClose()
              positiveHandler()
            }}
          />
        ) : null}
      </div>
    )
  }
}

export default DeleteDialog
