import IconButton from '@material-ui/core/IconButton'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import { createStyles, withStyles, WithStyles } from '@material-ui/core/styles'
import { Theme } from '@material-ui/core/styles/createMuiTheme'
import Tooltip from '@material-ui/core/Tooltip'
import MoreVertIcon from '@material-ui/icons/MoreVert'
import React from 'react'

// const ITEM_HEIGHT = 48;

const styles = (theme: Theme) =>
  createStyles({
    icon: {
      marginRight: 10,
    },
  })

interface ICreateMenuItemProps extends WithStyles<typeof styles> {
  icon: any
  content: any
  tooltip?: string
}
interface IEnhancedMenuProps {
  options: any[]
}

export const CreateMenuItem = withStyles(styles)(
  (props: ICreateMenuItemProps) => {
    const { classes, icon, content, tooltip, ...restProps } = props
    const jsx = React.cloneElement(
      <React.Fragment>
        {React.cloneElement(icon, { className: classes.icon })} {content}
      </React.Fragment>,
    )
    return (
      <MenuItem {...restProps}>
        {tooltip ? <Tooltip title={tooltip}>{jsx}</Tooltip> : jsx}
      </MenuItem>
    )
  },
)

class EnhancedMenu extends React.Component<IEnhancedMenuProps> {
  state = {
    anchorEl: null,
  }

  handleClick = (event: any) => {
    this.setState({ anchorEl: event.currentTarget })
  }

  handleClose = () => {
    this.setState({ anchorEl: null })
  }

  render() {
    const { anchorEl } = this.state
    const open = Boolean(anchorEl)
    const { options } = this.props
    return (
      <div>
        <IconButton
          aria-label='More'
          aria-owns={open ? 'long-menu' : undefined}
          aria-haspopup='true'
          title='More'
          onClick={this.handleClick}
        >
          <MoreVertIcon />
        </IconButton>
        <Menu
          id='long-menu'
          anchorEl={anchorEl}
          open={open}
          onClose={this.handleClose}
          PaperProps={{
            style: {
              //   maxHeight: ITEM_HEIGHT * 4.5,
              width: 200,
            },
          }}
        >
          {options.map((option, index) => (
            <React.Fragment key={index}>
              {option({ onClick: this.handleClose })}
            </React.Fragment>
          ))}
        </Menu>
      </div>
    )
  }
}

export default EnhancedMenu
