import Checkbox from '@material-ui/core/Checkbox'
import Paper from '@material-ui/core/Paper'
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from '@material-ui/core/styles'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TablePagination from '@material-ui/core/TablePagination'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import React from 'react'
import commanRender from '../../../utils/commanRender'
import formatDate from '../../../utils/date'
import filter from '../../../utils/filter'
import Loader from '../../Loader'
import EnhancedTableHead from './Head'
import EnhancedTableToolbar from './Toolbar'

// function getSorting(order, orderBy) {
//     return order === 'desc'
//         ? (a, b) => (b[orderBy] < a[orderBy] ? -1 : 1)
//         : (a, b) => (a[orderBy] < b[orderBy] ? -1 : 1);
// }

function* getColor({ primary = '#35A7FF', secondary = '#FF5964' } = {}) {
  let color = primary
  while (true) {
    color = color === secondary ? primary : secondary
    yield color
  }
}

const styles = (theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
      marginTop: theme.spacing.unit * 3,
    },
    table: {
      minWidth: '100%',
    },
    tableWrapper: {
      overflowX: 'auto',
    },
  })

interface IEnhancedTable extends WithStyles<typeof styles> {
  mutate: (variables: any) => void
  data: any[]
  loading?: boolean
  count: number
  settings: {
    order: string
    orderBy: string
    selected: string[]
    rowsPerPage: number
    page: number
  }
  skipToolbar?: boolean
  title: string
  columnData: any[]
  exclude?: any[]
  historyable?: string
  credit?: any
  menu?: any
  options?: {
    onEdit?: Function
    onView?: Function
    onDelete?: Function
    onCredit?: Function
    onAdd?: (e?: any) => void
    selectable?: boolean
  }
  primaryButtonIcon?: any
  customColumnData?: Function
  customRootClasses?: string[]
  skipHistoryColumns?: string[]
  render?: Function
}
class CustomTable extends React.PureComponent<IEnhancedTable> {
  handleRequestSort = async (property: string) => {
    const orderBy = property
    let order = 'asc'

    if (
      this.props.settings.orderBy === property &&
      this.props.settings.order === 'asc'
    ) {
      order = 'desc'
    }
    this.props.mutate({ ...this.props.settings, order, orderBy })
    // this.setprops({ order, orderBy });
  }

  handleSelectAllClick = async (e: any, checked: boolean) => {
    if (checked) {
      this.props.mutate({
        ...this.props.settings,
        selected: this.props.data.map(n => n.id),
      })
      // this.setprops(props => ({ selected: props.data.map(n => n.id) }));
      return
    }
    this.props.mutate({ ...this.props.settings, selected: [] })
    // this.setprops({ selected: [] });
  }

  handleClick = async (id: string) => {
    const { selected } = this.props.settings
    const selectedIndex = selected.indexOf(id)
    let newSelected: string[] = []

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id)
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1))
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1))
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      )
    }
    this.props.mutate({ ...this.props.settings, selected: newSelected })
  }

  handleChangePage = async (event: any, page: number) => {
    this.props.mutate({ ...this.props.settings, page, selected: [] })
  }

  isSelected = (id: string) => this.props.settings.selected.indexOf(id) !== -1

  dialogMutate = (dialogOpen: boolean) =>
    this.props.mutate({ ...this.props.settings, dialogOpen: !dialogOpen })
  render() {
    const {
      classes,
      loading,
      data,
      count,
      settings: { order, orderBy, selected = [], rowsPerPage, page },
      skipToolbar,
      title,
      columnData,
      exclude,
      historyable,
      credit,
      menu,
      options = {},
      customRootClasses = [],
      skipHistoryColumns,
      customColumnData,
      render = commanRender({}),
    } = this.props
    const {
      onEdit,
      onView = onEdit,
      onDelete,
      onCredit,
      onAdd,
      selectable = onEdit || onView || onDelete || onCredit ? true : false,
    } = options
    const newop = {
      onEdit,
      onView,
      onDelete,
      onCredit,
      onAdd,
    }
    const renderData: any[] = [].concat(data as any)
    // .sort(getSorting(order, orderBy));
    return (
      <Paper className={[...customRootClasses, classes.root].join(' ')}>
        {loading ? <Loader /> : null}
        {skipToolbar ? null : (
          <EnhancedTableToolbar
            title={title}
            skipHistoryColumns={skipHistoryColumns}
            numSelected={selected.length}
            selected={selected}
            menu={menu}
            historyable={historyable}
            credit={credit}
            options={newop}
            primaryButtonIcon={this.props.primaryButtonIcon}
          />
        )}
        <div className={classes.tableWrapper}>
          <Table className={classes.table} aria-labelledby={title}>
            <EnhancedTableHead
              selectable={selectable}
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={this.handleSelectAllClick}
              onRequestSort={this.handleRequestSort}
              rowCount={data.length || 1}
              columnData={columnData}
            />
            <TableBody>
              {(renderData.length &&
                renderData.map(({ createdAt, ...row }, mapIndex) => {
                  const isSelected = this.isSelected(row.id)
                  return (
                    <TableRow
                      hover={true}
                      role='checkbox'
                      aria-checked={isSelected}
                      tabIndex={-1}
                      key={row.id || mapIndex}
                      selected={isSelected}
                      onDoubleClick={() => onView && onView(row.id)}
                    >
                      {selectable ? (
                        <TableCell padding='checkbox'>
                          <Checkbox
                            checked={isSelected}
                            onClick={() => this.handleClick(row.id)}
                          />
                        </TableCell>
                      ) : null}
                      {createdAt ? (
                        <TableCell
                          component='th'
                          scope='row'
                          padding={!selectable ? 'default' : 'none'}
                        >
                          {formatDate(createdAt)}
                        </TableCell>
                      ) : null}
                      {(() => {
                        const rest = filter(row, exclude)
                        const color = getColor()
                        return (
                          <React.Fragment>
                            {Object.keys(rest).map(e => {
                              const colorValue = color.next().value
                              return render(e, rest[e], colorValue)
                            })}
                            {customColumnData ? (
                              <TableCell
                                style={{ color: color.next().value }}
                                key={row.id || Math.random()}
                              >
                                {customColumnData(rest)}
                              </TableCell>
                            ) : null}
                          </React.Fragment>
                        )
                      })()}
                    </TableRow>
                  )
                })) || (
                <TableRow tabIndex={-1}>
                  <TableCell colSpan={columnData.length + 1}>
                    <Typography
                      align='center'
                      color='textSecondary'
                      variant='subheading'
                    >
                      No data yet.
                    </Typography>
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </div>
        <TablePagination
          component='div'
          count={count}
          rowsPerPage={rowsPerPage}
          rowsPerPageOptions={[15]}
          page={page}
          backIconButtonProps={{
            'aria-label': 'Previous Page',
            title: 'Previous Page',
          }}
          nextIconButtonProps={{
            'aria-label': 'Next Page',
            title: 'Next Page',
          }}
          onChangePage={this.handleChangePage}
        />
      </Paper>
    )
  }
}

const EnhancedTable = withStyles(styles)(CustomTable)
export default EnhancedTable
