import { ApolloClient, ApolloLink, InMemoryCache } from 'apollo-boost'
import { BatchHttpLink } from 'apollo-link-batch-http'
import { setContext } from 'apollo-link-context'
import { withClientState } from 'apollo-link-state'
import { WatchedMutationLink } from 'apollo-link-watched-mutation'
import React, { Component } from 'react'
import { ApolloProvider } from 'react-apollo'
import { BrowserRouter as Router } from 'react-router-dom'
import './App.css'
import { defaultState, resolvers, typeDefs } from './graphql/resolvers'
import './Index.css'
import MyRouter from './Router'
import WM from './WatchedMutation'

const cache = new InMemoryCache({
  dataIdFromObject: obj => obj.id,
})

// use this to logout on tab close.
// window.onbeforeunload = function () {
//     localStorage.clear();
// }

const watched = new WatchedMutationLink(
  cache, // whatever is passed to the apollo-client
  {
    ...WM,
  },
)

const stateLink = withClientState({
  typeDefs,
  defaults: defaultState,
  resolvers,
})

const authLink = setContext((_, { headers }) => {
  // get the authentication token from local storage if it exists
  const token = localStorage.getItem('userToken')
  // return the headers to the context so httpLink can read them
  return {
    headers: {
      ...headers,
      authorization: token ? `${token}` : '',
    },
  }
})

const client = new ApolloClient({
  link: ApolloLink.from([
    watched,
    authLink,
    stateLink,
    // new HttpLink({
    //   uri: '/graphql',
    // }),
    new BatchHttpLink({
      uri: '/graphql',
      headers: { batch: true },
    }),
  ]),
  cache,
})

class App extends Component {
  render() {
    return (
      <ApolloProvider client={client}>
        <Router>
          <MyRouter />
        </Router>
      </ApolloProvider>
    )
  }
}

export default App
