import React, { Component } from 'react'

import gql from 'graphql-tag'
import { Redirect } from 'react-router'
import { LOCAL_LOGIN } from '../graphql/query/local'
import parseJWT from '../utils/parseJWT'
import Body from './Body'
import Footer from './Footer'
import Login from './forms/Login'
import Header from './Header'
import Query from './Query'

class Layout extends Component {
  render() {
    return (
      <Query query={LOCAL_LOGIN}>
        {({ data }: IGraphqlData) => {
          let user: any = null
          if (data.localLogin && data.localLogin.token) {
            user = parseJWT(data.localLogin.token)
            user.isAdmin = () => Number(user.role) === 1
            user.isManager = () => Number(user.role) === 0
          }
          return (
            <React.Fragment>
              <Query
                query={gql`
                  query {
                    me {
                      id
                      username
                      role
                      stockPermission
                    }
                  }
                `}
                noError={true}
              >
                {(args: IGraphqlData) => {
                  const { loading, data: qdata, error, client } = args
                  if (!qdata || !qdata.me || error) {
                    if (
                      error.message &&
                      error.message.includes('Not Authenticated')
                    ) {
                      setTimeout(async () => {
                        try {
                          await window.localStorage.clear()
                          if ('serviceWorker' in navigator) {
                            const keys = await caches.keys()
                            keys.forEach(cacheName => {
                              caches.delete(cacheName)
                            })
                          }
                          client
                            .resetStore()(this.props as any)
                            .history.push('/login')
                        } catch (ex) {
                          console.log(ex)
                        }
                      }, 0)
                    }
                    return <Redirect to={'/404'} />
                  }
                  const { me } = qdata
                  if (loading) {
                    return <span>loading...</span>
                  } else if (error) {
                    return <span>Error Occured! Try Logging In again.</span>
                  }
                  if (me && me.id) {
                    return (
                      <>
                        <Header
                          {...this.props}
                          client={client}
                          user={user}
                          me={me}
                        />
                        <Body me={me}>{this.props.children}</Body>
                        <Footer />
                      </>
                    )
                  } else {
                    return (
                      <>
                        <Header {...this.props} client={client} user={user} />
                        <Body>
                          <Login />
                        </Body>
                        <Footer />
                      </>
                    )
                  }
                }}
              </Query>
            </React.Fragment>
          )
        }}
      </Query>
    )
  }
}

export default Layout
