import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import React from 'react'

const Footer = () => {
  return (
    <Grid
      container={true}
      alignContent='center'
      alignItems='center'
      justify='center'
      className='silver bg-dark-gray pv4'
    >
      <Grid item={true}>
        <Typography color='inherit'>
           {new Date().getFullYear()} Codemitter Technology Pvt. Ltd.
        </Typography>
      </Grid>
    </Grid>
  )
}

export default Footer
