import { gql } from 'apollo-boost'
import { siteTotalRepeater } from '../query/remote'
export const LOGIN = gql`
  mutation login($data: LoginInput!) {
    login(data: $data) {
      token
    }
  }
`

export const CREDIT = gql`
  mutation credit($id: String!, $amount: Float!, $note: String) {
    credit(id: $id, amount: $amount, note: $note) {
      amount
      createdAt
      note
    }
  }
`

export const CREATE_SITE = gql`
mutation createSite($data: SiteInput!) {
    createSite(data: $data) {
        id,
        name,
        location,
        entryCount,
        count
        manager {
            id,
            name: username
        }
        managerSpentAmount,
        cost
        total{
            mistri ${siteTotalRepeater}
            labour ${siteTotalRepeater}
            eit ${siteTotalRepeater}
            morang ${siteTotalRepeater}
            baalu ${siteTotalRepeater}
            githi ${siteTotalRepeater}
            cement ${siteTotalRepeater}
            saria ${siteTotalRepeater}
            dust ${siteTotalRepeater}
            other
            other2
        }
        createdAt,
        updatedAt,
    }
}
`

export const UPDATE_SITE = gql`
mutation updateSite($id: String!, $data: SiteUpdateInput!) {
    updateSite(id: $id, data: $data) {
        id,
        name,
        location,
        entryCount,
        manager {
            id,
            name: username
        }
        managerSpentAmount,
        cost
        total{
            mistri ${siteTotalRepeater}
            labour ${siteTotalRepeater}
            eit ${siteTotalRepeater}
            morang ${siteTotalRepeater}
            baalu ${siteTotalRepeater}
            githi ${siteTotalRepeater}
            cement ${siteTotalRepeater}
            saria ${siteTotalRepeater}
            dust ${siteTotalRepeater}
            other
            other2
        }
        createdAt,
        updatedAt,
    }
}
`

const repeater = gql`
  {
    quantity
    cost
    paid
  }
`

export const CREATE_SITE_ENTRY = gql`
mutation createSiteEntry(
    $siteId: String!,
    $data: SiteEntryInput!
) {
    createSiteEntry(
        siteId: $siteId,
        data: $data
    ) {
        id,
        createdAt,
        updatedAt,
        mistri ${repeater},
        labour ${repeater},
        eit ${repeater},
        morang ${repeater},
        baalu ${repeater},
        githi ${repeater},
        cement ${repeater},
        saria ${repeater},
        dust ${repeater},
        other ${repeater},
        other2 ${repeater},
        site {
            id
        }
        managerSpentAmount,
        total
    }
}
`

export const UPDATE_SITE_ENTRY = gql`
mutation updateSiteEntry(
    $id: String!
    $siteId: String!,
    $data: SiteEntryInput!
) {
    updateSiteEntry(
        id: $id,
        siteId: $siteId,
        data: $data
    ) {
        id,
        createdAt,
        updatedAt,
        mistri ${repeater},
        labour ${repeater},
        eit ${repeater},
        morang ${repeater},
        baalu ${repeater},
        githi ${repeater},
        cement ${repeater},
        saria ${repeater},
        dust ${repeater},
        other ${repeater},
        other2 ${repeater},
        site {
            id
        }
        managerSpentAmount,
        total
    }
}
`

