function creditArray({
  queryName,
  mutationName,
}: {
  queryName: string
  mutationName: string
}) {
  return ({ mutation, query }: any) => {
    if (mutation.result.errors) {
      return query.result
    }
    const newHistory = mutation.result.data[mutationName]
    const id = mutation.variables.id
    const item = query.result[queryName].find((e: any) => e.id === id)
    if (!item) {
      return query.result
    }
    item.totalReceivedAmount += newHistory.amount
    item.balance += newHistory.amount
    return query.result
  }
}

function creditItem({
  queryName,
  mutationName,
}: {
  queryName: string
  mutationName: string
}) {
  return ({ mutation, query }: any) => {
    if (mutation.result.error) {
      return query.result
    }
    if (query.variables.skip !== 0) {
      query.result[queryName][0].count++
      return query.result
    }
    const newHistory = mutation.result.data[mutationName]
    if (newHistory) {
      query.result[queryName].unshift({
        count: query.result[queryName][0].count + 1,
        ...newHistory,
      })
    }
    return query.result
  }
}

export default {
  createUser: {
    users: ({ mutation, query }: any) => {
      if (mutation.result.errors) {
        return query.result
      }
      const newUser = mutation.result.data.createUser
      if (newUser && newUser.id) {
        query.result.users.unshift(newUser)
      }
      return query.result
    },
  },
  credit: {
    users: creditArray({
      queryName: 'users',
      mutationName: 'credit',
    }),
    userCreditHistory: creditItem({
      queryName: 'userCreditHistory',
      mutationName: 'credit',
    }),
  },
  createSite: {
    sites: ({ mutation, query }: any) => {
      if (mutation.result.errors) {
        return query.result
      }
      const newSite = mutation.result.data.createSite
      if (newSite && newSite.id) {
        query.result.sites.unshift(newSite)
      }
      return query.result
    },
  },
  createSiteEntry: {
    siteEntries: ({ mutation, query }: any) => {
      if (mutation.result.errors) {
        return query.result
      }
      const ctx = JSON.parse(window.localStorage.getItem('ctx') || '{}')
      if (ctx && ctx.siteEntryInsert) {
        // window.localStorage.removeItem("ctx");
        return query.result
      }
      const newEntry = mutation.result.data.createSiteEntry
      if (newEntry && newEntry.id) {
        query.result.siteEntries.unshift(newEntry)
      }
      window.localStorage.setItem(
        'ctx',
        JSON.stringify({
          siteEntryInsert: true,
          done: false,
        }),
      )
      return query.result
    },
    sites: ({ mutation, query }: any) => {
      if (mutation.result.errors) {
        return query.result
      }
      const ctx = JSON.parse(window.localStorage.getItem('ctx') || '{}')
      if (ctx.done || query.variables.skip !== 0) {
        // window.localStorage.removeItem("ctx");
        return query.result
      }
      const newEntry = mutation.result.data.createSiteEntry
      const site = query.result.sites.find(
        (e: any) => e.id === newEntry.site.id,
      )
      if (!site) {
        return query.result
      }
      // tslint:disable-next-line:prefer-const
      let { other, other2, __typename, ...total } = site.total
      Object.keys(total).forEach((e: any) => {
        total[e].quantity += newEntry[e].quantity
        total[e].cost += newEntry[e].cost
      })
      other += newEntry.other.cost
      other2 += newEntry.other2.cost

      site.entryCount += 1
      site.cost += newEntry.total
      site.managerSpentAmount += newEntry.managerSpentAmount
      window.localStorage.setItem(
        'ctx',
        JSON.stringify({
          done: true,
          userDone: false,
          site,
        }),
      )
      return query.result
    },
    site: ({ mutation, query }: any) => {
      if (mutation.result.errors) {
        return query.result
      }
      const ctx = JSON.parse(window.localStorage.getItem('ctx') || '{}')
      if (ctx.done) {
        // window.localStorage.removeItem("ctx");
        return query.result
      }
      const newEntry = mutation.result.data.createSiteEntry
      const site =
        query.result.site.id === newEntry.site.id ? query.result.site : null
      if (!site) {
        return query.result
      }
      // tslint:disable-next-line:prefer-const
      let { other, other2, __typename, ...total } = site.total
      Object.keys(total).forEach((e: any) => {
        total[e].quantity += newEntry[e].quantity
        total[e].cost += newEntry[e].cost
      })
      other += newEntry.other.cost
      other2 += newEntry.other2.cost

      site.entryCount += 1
      site.cost += newEntry.total
      site.managerSpentAmount += newEntry.managerSpentAmount
      window.localStorage.setItem(
        'ctx',
        JSON.stringify({
          done: true,
          userDone: false,
          site,
        }),
      )
      return query.result
    },
    users: ({ mutation, query }: any) => {
      if (mutation.result.errors) {
        return query.result
      }
      const ctx = JSON.parse(window.localStorage.getItem('ctx') || '{}')
      if (ctx.userDone && !ctx.site) {
        window.localStorage.setItem(
          'ctx',
          JSON.stringify({
            done: true,
            userDone: true,
          }),
        )
        return query.result
      }
      const newEntry = mutation.result.data.createSiteEntry
      const user = query.result.users.find(
        (e: any) => e.id === ctx.site.manager.id,
      )
      if (!user) {
        window.localStorage.setItem(
          'ctx',
          JSON.stringify({
            done: true,
            userDone: true,
          }),
        )
        return query.result
      }
      user.spent += newEntry.managerSpentAmount
      user.siteCount++
      user.totalSitesCost += newEntry.total
    },
  },
  updateSiteEntry: {
    siteEntries: ({ mutation, query }: any) => {
      if (mutation.result.errors) {
        return query.result
      }
      const updatedEntry = mutation.result.data.updateSiteEntry
      const oldEntry = query.result.siteEntries.find(
        (e: any) => e.id === updatedEntry.id,
      )
      if (!oldEntry) {
        window.localStorage.setItem(
          'ctx',
          JSON.stringify({
            done: false,
          }),
        )
        return query.result
      }
      window.localStorage.setItem(
        'ctx',
        JSON.stringify({
          done: false,
          oldEntry,
        }),
      )

      return query.result
    },
    sites: ({ mutation, query }: any) => {
      if (mutation.result.errors) {
        return query.result
      }
      const ctx = JSON.parse(window.localStorage.getItem('ctx') || '{}')
      if (ctx.done) {
        return query.result
      }
      const updatedEntry = mutation.result.data.updateSiteEntry
      const site = query.result.sites.find(
        (e: any) => e.id === updatedEntry.site.id,
      )
      if (!site) {
        window.localStorage.setItem(
          'ctx',
          JSON.stringify({
            done: false,
            oldEntry: ctx.oldEntry,
          }),
        )
        return query.result
      }
      try {
        const { oldEntry } = ctx

        // tslint:disable-next-line:prefer-const
        let { other, other2, __typename, ...total } = site.total
        Object.keys(total).forEach((e: any) => {
          total[e].quantity =
            total[e].quantity - oldEntry[e].quantity + updatedEntry[e].quantity
          total[e].cost =
            total[e].cost - oldEntry[e].cost + updatedEntry[e].cost
        })
        other = other - oldEntry.other.cost + updatedEntry.other.cost
        other2 = other2 - oldEntry.other2.cost + updatedEntry.other2.cost

        // const total = site.total;
        // Object.keys(total).forEach((e: any) => {
        //   total[e] = total[e] - oldEntry[e].cost + updatedEntry[e].cost;
        // });

        site.cost = site.cost - oldEntry.total + updatedEntry.total
        site.managerSpentAmount =
          site.managerSpentAmount -
          oldEntry.managerSpentAmount +
          updatedEntry.managerSpentAmount
        window.localStorage.setItem(
          'ctx',
          JSON.stringify({
            done: true,
          }),
        )
      } catch (e) {
        return query.result
      }
      return query.result
    },
    site: ({ mutation, query }: any) => {
      if (mutation.result.errors) {
        return query.result
      }
      const ctx = JSON.parse(window.localStorage.getItem('ctx') || '{}')
      if (ctx.done) {
        // window.localStorage.removeItem("ctx");
        return query.result
      }
      const { oldEntry } = ctx
      const updatedEntry = mutation.result.data.updateSiteEntry
      const site =
        query.result.site.id === updatedEntry.site.id ? query.result.site : null
      if (!site) {
        // window.localStorage.removeItem('ctx');
        window.localStorage.setItem(
          'ctx',
          JSON.stringify({
            done: false,
          }),
        )
        return query.result
      }
      // tslint:disable-next-line:prefer-const
      let { other, other2, __typename, ...total } = site.total
      Object.keys(total).forEach((e: any) => {
        total[e].quantity =
          total[e].quantity - oldEntry[e].quantity + updatedEntry[e].quantity
        total[e].cost = total[e].cost - oldEntry[e].cost + updatedEntry[e].cost
      })
      other = other - oldEntry.other.cost + updatedEntry.other.cost
      other2 = other2 - oldEntry.other2.cost + updatedEntry.other2.cost

      site.cost = site.cost - oldEntry.total + updatedEntry.total
      site.managerSpentAmount =
        site.managerSpentAmount -
        oldEntry.managerSpentAmount +
        updatedEntry.managerSpentAmount
      window.localStorage.setItem(
        'ctx',
        JSON.stringify({
          done: true,
        }),
      )
      return query.result
    },
  },
  createProperty: {
    properties: ({ mutation, query }: any) => {
      if (mutation.result.errors) {
        return query.result
      }
      const newProperty = mutation.result.data.createProperty
      if (newProperty && newProperty.id) {
        query.result.properties.unshift(newProperty)
      }
      return query.result
    },
  },
  propertyCredit: {
    properties: creditArray({
      queryName: 'properties',
      mutationName: 'propertyCredit',
    }),
    propertyCreditHistory: creditItem({
      queryName: 'propertyCreditHistory',
      mutationName: 'propertyCredit',
    }),
  },
}
