import { FormHelperText } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import CircularProgress from '@material-ui/core/CircularProgress'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import IconButton from '@material-ui/core/IconButton'
import InputAdornment from '@material-ui/core/InputAdornment'
import { withStyles } from '@material-ui/core/styles'
import AttachMoneyIcon from '@material-ui/icons/AttachMoney'
import { Form } from 'informed'
import React from 'react'
import { CREDIT } from '../../graphql/mutation/remote'
import { getJSX } from '../forms/FormGenerator'
import Mutation from '../Mutation'
import CommanDialog, { IProps, styles } from './CommanDialog'

const gdefaults = {
  amount: {
    type: 'number',
    default: 0,
    autoFocus: true,
    startAdornment: <InputAdornment position='start'></InputAdornment>,
  },
  note: {
    type: 'text',
    default: '',
    // startAdornment: <InputAdornment position='start'></InputAdornment>,
  },
}
export default withStyles(styles)(
  class FormDialog extends CommanDialog<
    IProps & { mutation?: string; defaults?: any }
  > {
    render() {
      const { open } = this.state
      const {
        classes,
        history,
        negativeHandler = () => this.handleClose(),
        positiveHandler,
        postMutation,
        mutation,
        defaults = gdefaults,
      } = this.props
      const title = 'Add Credit'
      const content = 'Add credit amount to this user.'
      return (
        <div>
          <Mutation mutation={mutation || CREDIT}>
            {(mutate: Function, { loading, error }: IGraphqlData) => {
              return (
                <React.Fragment>
                  <IconButton
                    aria-label='Credit'
                    title='Credit'
                    onClick={this.handleOpen}
                  >
                    <AttachMoneyIcon />
                  </IconButton>
                  {open ? (
                    <Dialog
                      fullScreen={false}
                      open={open}
                      onClose={negativeHandler}
                      aria-labelledby={title}
                      title={title}
                    >
                      <Form
                        onSubmit={(e: any) => {
                          positiveHandler(mutate, e).then((data: any) => {
                            if (postMutation) {
                              postMutation(data)
                              this.handleClose()
                            } else {
                              history.goBack()
                            }
                          })
                        }}
                      >
                        <DialogTitle id={title}>{title}</DialogTitle>
                        <DialogContent>
                          <DialogContentText>{content}</DialogContentText>
                          {getJSX(defaults)}
                          {error && (
                            <FormHelperText error={true}>
                              {error.message}
                            </FormHelperText>
                          )}
                        </DialogContent>
                        <DialogActions>
                          {loading ? (
                            <CircularProgress className={classes.progress} />
                          ) : null}
                          <Button
                            onClick={negativeHandler}
                            color='primary'
                            autoFocus={true}
                          >
                            Cancel
                          </Button>
                          <Button size='large' type='submit' disabled={loading}>
                            Confirm
                          </Button>
                        </DialogActions>
                      </Form>
                    </Dialog>
                  ) : null}
                </React.Fragment>
              )
            }}
          </Mutation>
        </div>
      )
    }
  },
)
