import Button from '@material-ui/core/Button'
import CircularProgress from '@material-ui/core/CircularProgress'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import InputAdornment from '@material-ui/core/InputAdornment'
import { withStyles } from '@material-ui/core/styles'
import { Form } from 'informed'
import React from 'react'
import { UPDATE_USER_CONTACT } from '../../graphql/mutation/remote'
import { USER } from '../../graphql/query/remote'
import { getJSX } from '../forms/FormGenerator'
import Mutation from '../Mutation'
import Query from '../Query'
import CommanDialog, { IProps, styles } from './CommanDialog'

export default withStyles(styles)(
  class ChangeContact extends CommanDialog<IProps> {
    render() {
      const { open } = this.state
      const {
        classes,
        history,
        user,
        negativeHandler = (e: any) => this.handleClose(),
        positiveHandler,
        postMutation,
        children,
      } = this.props
      const title = 'Change contact'
      const content = 'Change contact amount to this user.'
      return (
        <div>
          <Query query={USER} variables={{ id: user.id }}>
            {({ data }: any) => {
              const defaults = {
                contact: {
                  type: 'number',
                  default: data.user.contact,
                  startAdornment: (
                    <InputAdornment position='start'>+91</InputAdornment>
                  ),
                  validate: (val: any) => {
                    return String(val).length !== 10
                      ? 'Invalid phone number'
                      : null
                  },
                },
              }
              return (
                <Mutation mutation={UPDATE_USER_CONTACT}>
                  {(
                    mutate: Function,
                    { loading, error, data }: IGraphqlData,
                  ) => {
                    return (
                      <React.Fragment>
                        {React.cloneElement(children, {
                          onClick: this.handleOpen,
                        })}
                        {/* <IconButton aria-label="Contact" onClick={this.handleOpen}>
                                                  <PhoneIcon /> {title}
                                              </IconButton> */}
                        {open ? (
                          <Dialog
                            fullScreen={false}
                            open={open}
                            onClose={negativeHandler}
                            aria-labelledby={title}
                            title={title}
                          >
                            <Form
                              onSubmit={(e: any) => {
                                positiveHandler(mutate, e).then((data: any) => {
                                  if (postMutation) {
                                    postMutation(data)
                                    this.handleClose()
                                  } else {
                                    history.goBack()
                                  }
                                })
                              }}
                            >
                              <DialogTitle id={title}>{title}</DialogTitle>
                              <DialogContent>
                                <DialogContentText>{content}</DialogContentText>
                                {getJSX(defaults)}
                              </DialogContent>
                              <DialogActions>
                                {loading ? (
                                  <CircularProgress
                                    className={classes.progress}
                                  />
                                ) : null}
                                <Button
                                  size='large'
                                  color='secondary'
                                  onClick={negativeHandler}
                                >
                                  Cancel
                                </Button>
                                <Button
                                  size='large'
                                  color='primary'
                                  type='submit'
                                  disabled={loading}
                                >
                                  Confirm
                                </Button>
                              </DialogActions>
                            </Form>
                          </Dialog>
                        ) : null}
                      </React.Fragment>
                    )
                  }}
                </Mutation>
              )
            }}
          </Query>
        </div>
      )
    }
  },
)

