import Drawer from '@material-ui/core/Drawer'
import IconButton from '@material-ui/core/IconButton'
import { createStyles, Theme, withStyles } from '@material-ui/core/styles'
import MenuIcon from '@material-ui/icons/Menu'
import classNames from 'classnames'
import React from 'react'
import List from './DrawerMenuList'

const styles = (theme: Theme) =>
  createStyles({
    list: {
      width: 250,
    },
    fullList: {
      width: 'auto',
    },
    menuButton: {
      marginLeft: -theme.spacing.unit,
      marginRight: theme.spacing.unit,
    },
    hide: {
      display: 'none',
    },
  })

interface IDrawerMenuState {
  top: boolean
  left: boolean
  bottom: boolean
  right: boolean
}

const DrawerMenu = withStyles(styles)(
  class extends React.Component<any, IDrawerMenuState> {
    state = {
      top: false,
      left: false,
      bottom: false,
      right: false,
    }

    toggleDrawer = (side: string, open: boolean) => () => {
      this.setState({
        ...this.state,
        [side]: open,
      })
    }

    render() {
      const { classes, ...restProps } = this.props
      const sideList = (
        <div className={classes.list}>
          <List {...restProps as never} />
        </div>
      )

      return (
        <React.Fragment>
          <IconButton
            color='inherit'
            aria-label='Open drawer'
            title='Open drawer'
            onClick={this.toggleDrawer('left', true)}
            className={classNames(
              classes.menuButton,
              this.state.left && classes.hide,
            )}
          >
            <MenuIcon />
          </IconButton>
          <Drawer
            open={this.state.left}
            onClose={this.toggleDrawer('left', false)}
          >
            <div
              tabIndex={0}
              role='button'
              onClick={this.toggleDrawer('left', false)}
              onKeyDown={this.toggleDrawer('left', false)}
            >
              {sideList}
            </div>
          </Drawer>
        </React.Fragment>
      )
    }
  },
)

export default DrawerMenu
