import FormControl from '@material-ui/core/FormControl'
import FormHelperText from '@material-ui/core/FormHelperText'
import InputLabel from '@material-ui/core/InputLabel'
import MenuItem from '@material-ui/core/MenuItem'
import Select from '@material-ui/core/Select'
import { asField } from 'informed'
import React from 'react'

export default asField(({ fieldState, fieldApi, ...props }: any) => {
  const { value, error } = fieldState
  const { setValue, setTouched } = fieldApi
  const {
    onChange,
    onBlur,
    forwardedRef,
    field: name,
    label = name,
    options,
    selected = null,
    mapping = {
      key: 'key',
      value: 'value',
    },
    ...rest
  } = props
  return (
    <FormControl fullWidth={true} className='mt3'>
      <InputLabel htmlFor={name} className='ttc'>
        {label}
      </InputLabel>
      <Select
        {...rest}
        ref={forwardedRef}
        value={value || ''}
        onChange={e => {
          setValue(e.target.value)
          if (onChange) {
            onChange(e)
          }
        }}
        onBlur={e => {
          setTouched()
          if (onBlur) {
            onBlur(e)
          }
        }}
        inputProps={{
          name,
        }}
        // input={<Input name={name} />}
      >
        {/* <MenuItem value="" selected>
                    <em>None</em>
                </MenuItem> */}
        {options.map((e: any) => {
          return mapping ? (
            <MenuItem value={e[mapping.value]} key={e[mapping.key]}>
              {e[mapping.key]}
            </MenuItem>
          ) : (
            <MenuItem value={e} key={e}>
              {e}
            </MenuItem>
          )
        })}
      </Select>
      {error ? <FormHelperText error={true}>{error}</FormHelperText> : null}
    </FormControl>
  )
})
