import React, { PureComponent } from 'react'
import Mutation from './Mutation'
import Query from './Query'

interface ITableWrapperProps {
  tableMutation: string
  tableQuery: string
  dataQuery: string
  defaultState: any
  resetOnMount?: boolean
  getQueryVariables?: (settings: any) => any
}
interface ITableWrapperState {
  reset: boolean
}
class TableWrapper extends PureComponent<
  ITableWrapperProps,
  ITableWrapperState
> {
  state = {
    reset: false,
  }
  componentWillMount() {
    this.setState({
      reset: true,
    })
  }
  render() {
    const { reset } = this.state
    const {
      tableMutation,
      tableQuery,
      dataQuery,
      getQueryVariables = () => ({}),
      defaultState,
      resetOnMount = false,
    } = this.props
    return (
      <Mutation mutation={tableMutation}>
        {(lmutate: Function) => {
          if (resetOnMount && reset) {
            lmutate({ variables: defaultState })
            this.setState({ reset: false })
          }
          return (
            <Query query={tableQuery}>
              {({ data }: IGraphqlData) => {
                const tableSettings = data[Object.keys(data)[0]]
                const { page, rowsPerPage } = tableSettings
                return (
                  <Query
                    query={dataQuery}
                    variables={{
                      limit: rowsPerPage,
                      skip: page * rowsPerPage,
                      ...getQueryVariables(tableSettings),
                    }}
                  >
                    {(props: any) => {
                      return (
                        this.props.children instanceof Function &&
                        this.props.children(tableSettings, lmutate, props)
                      )
                    }}
                  </Query>
                )
              }}
            </Query>
          )
        }}
      </Mutation>
    )
  }
}

export default TableWrapper
