import { gql } from 'apollo-boost'

export const LOCAL_LOGIN = gql`
  mutation localLogin($token: String!) {
    localLogin(token: $token) @client {
      token
    }
  }
`

function tableMutationGenerator(name: string): string {
  return gql`
    mutation ${name} (
        $order: String,
        $orderBy: String,
        $selected: [String],
        $page: Int,
        $rowsPerPage: Int
    )
    {
        ${name} (
            order: $order,
            orderBy: $orderBy,
            selected: $selected,
            page: $page,
            rowsPerPage: $rowsPerPage) @client {
                order,
                orderBy,
                selected,
                page,
                rowsPerPage,
            }
    }
`
}
export const ENTRY_TABLE = tableMutationGenerator('entryTable')

export const SITE_TABLE = tableMutationGenerator('siteTable')

export const USER_ENTRY_TABLE = tableMutationGenerator('userEntryTable')

export const CREDIT_ENTRY_TABLE = tableMutationGenerator('creditEntryTable')

export const PROPERTY_TABLE = tableMutationGenerator('propertyTable')

export const DUE_PROPERTY_TABLE = tableMutationGenerator('duePropertyTable')

export const STOCK_ITEM_TABLE = tableMutationGenerator('stockItemTable')

export const STOCK_ITEM_TRANSACTION_TABLE = tableMutationGenerator(
  'stockItemTransactionTable',
)

export const SUPPLIER_TRANSACTION_TABLE = tableMutationGenerator(
  'supplierTransactionTable',
)

export const STOCK_SUPPLIER_TABLE = tableMutationGenerator('stockSupplierTable')

export const STOCK_TRANSACTION_TABLE = tableMutationGenerator(
  'stockTransactionTable',
)

export const ENHANCED_TABS = gql`
  mutation enhancedTabs($value: Int!) {
    enhancedTabs(value: $value) @client {
      value
    }
  }
`

export const STOCK_ENHANCED_TABS = gql`
  mutation stockEnhancedTabs($value: Int!) {
    stockEnhancedTabs(value: $value) @client {
      value
    }
  }
`
