import React, { PureComponent } from 'react'
import { Redirect } from 'react-router-dom'
import Layout from '../components/Layout'
import { LOCAL_LOGIN } from '../graphql/query/local'
import { Routes } from '../Router'
import parseJWT from '../utils/parseJWT'
import Query from './Query'

const asyncImport = (importComponent: any) => {
  return class extends PureComponent {
    state = {
      component: null,
    }

    componentWillMount() {
      importComponent().then((cmp: any) => {
        this.setState({ component: cmp.default })
      })
    }
    render() {
      const { skipLayout }: { skipLayout: any } = this.props as any
      const C: any = this.state.component
      const result = (lprops: any) => (
        <Query query={LOCAL_LOGIN}>
          {({
            loading,
            error,
            data,
          }: {
            loading: boolean
            error: any
            data: any
          }) => {
            if (loading) {
              return <span>loading...</span>
            } else if (error) {
              return <span>Error Occured! Try Logging In again.</span>
            }
            if (data.localLogin && data.localLogin.token) {
              const user = parseJWT(data.localLogin.token)
              user.isAdmin = () => Number(user.role) === 1
              user.isManager = () => Number(user.role) === 0
              return <C {...this.props} user={user} {...lprops} />
            } else {
              return <Redirect to={Routes.login()} />
            }
          }}
        </Query>
      )
      return C ? (
        !skipLayout ? (
          <Layout {...this.props}>{result}</Layout>
        ) : (
          result({})
        )
      ) : null
    }
  }
}

export default asyncImport
