import { createStyles, WithStyles } from '@material-ui/core'
import { Theme } from '@material-ui/core/styles/createMuiTheme'
import React from 'react'

export const styles = (theme: Theme) =>
  createStyles({
    progress: {
      margin: '1rem',
    },
  })

export interface ICommanDialogProps {
  history: any
  user: any
  negativeHandler?: () => void
  positiveHandler: (...rest: any[]) => any
  postMutation?: Function
  children?: any
}
interface ICommanDialogState {
  open: boolean
}

abstract class CommanDialog<
  P extends ICommanDialogProps
> extends React.PureComponent<P, ICommanDialogState> {
  state: ICommanDialogState = {
    open: false,
  }
  static defaultProps: Partial<ICommanDialogProps> = {
    positiveHandler: (...rest: any[]) => null,
    postMutation: (e: any) => null,
  }
  protected handleClose = () => {
    this.setState({ open: false })
  }

  protected handleOpen = () => {
    this.setState({ open: true })
  }

  abstract render(): any
}
// const s = (t: Theme) => createStyles({});
const s = {}
export interface IProps<P extends typeof s = typeof styles>
  extends ICommanDialogProps,
    WithStyles<P> {}
export default CommanDialog
