import React, { PureComponent } from 'react'
import { Redirect } from 'react-router-dom'
import { LOCAL_LOGIN } from '../../graphql/mutation/local'
import { LOGIN } from '../../graphql/mutation/remote'
import { LOCAL_LOGIN as LOCAL_LOGIN_QUERY } from '../../graphql/query/local'
import { Routes } from '../../Router'
import Mutation from '../Mutation'
import Query from '../Query'
import FormGenerator from './FormGenerator'

class Login extends PureComponent {
  render() {
    return (
      <Query query={LOCAL_LOGIN_QUERY}>
        {({ data }: IGraphqlData) => {
          if (data.localLogin && data.localLogin.token) {
            return <Redirect to={Routes.root()} />
          }
          return (
            <Mutation mutation={LOCAL_LOGIN}>
              {(locallogin: Function) => {
                const defaults = {
                  username: {
                    type: 'text',
                    default: '',
                    required: true,
                  },
                  password: {
                    type: 'password',
                    default: '',
                    required: true,
                  },
                }
                return (
                  <FormGenerator
                    {...this.props}
                    defaults={defaults}
                    submitText='Login'
                    mutation={LOGIN}
                    postMutation={({ data }: IGraphqlData) => {
                      const {
                        login: { token },
                      } = data
                      window.localStorage.setItem('userToken', token)
                      locallogin({ variables: { token } }).then(
                        ({ data }: IGraphqlData) => {},
                      )
                    }}
                    title='Login'
                  />
                )
              }}
            </Mutation>
          )
        }}
      </Query>
    )
  }
}

export default Login
