import React from 'react'
import { Link } from 'react-router-dom'

const getContactJSX = (value: number) =>
  value ? (
    <Link
      to={''}
      onClick={e =>
        setTimeout(e => (window.location.href = `tel:+91-${value}`), 0)
      }
    >
      {value}
    </Link>
  ) : (
    '-'
  )
export default getContactJSX
